"use client";

import { useState, useEffect, useCallback } from 'react';

export interface User {
id: number;
name: string;
email: string;
}

export const useSQLite = () => {
const [users, setUsers] = useState<User[]>([]);
const [isLoading, setIsLoading] = useState(true);

// –ó–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö –∏–∑ localStorage –ø—Ä–∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏
useEffect(() => {
    const loadFromStorage = () => {
    try {
        const stored = localStorage.getItem('sqlite_users');
        if (stored) {
        setUsers(JSON.parse(stored));
        } else {
        // –ù–∞—á–∞–ª—å–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ
        const initialUsers: User[] = [
            { id: 1, name: '–ò–≤–∞–Ω –ò–≤–∞–Ω–æ–≤', email: 'ivan@example.com' },
            { id: 2, name: '–ú–∞—Ä–∏—è –°–∏–¥–æ—Ä–æ–≤–∞', email: 'maria@example.com' }
        ];
        setUsers(initialUsers);
        localStorage.setItem('sqlite_users', JSON.stringify(initialUsers));
        }
    } catch (error) {
        console.error('Error loading users:', error);
    } finally {
        setIsLoading(false);
    }
    };

    loadFromStorage();
}, []);

// –°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –≤ localStorage
const saveToStorage = useCallback((newUsers: User[]) => {
    try {
    localStorage.setItem('sqlite_users', JSON.stringify(newUsers));
    } catch (error) {
    console.error('Error saving users:', error);
    }
}, []);

// –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
const addUser = useCallback((name: string, email: string) => {
    const newUser: User = {
    id: Date.now(),
    name,
    email
    };
    
    const newUsers = [...users, newUser];
    setUsers(newUsers);
    saveToStorage(newUsers);
}, [users, saveToStorage]);

// –£–¥–∞–ª–µ–Ω–∏–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
const deleteUser = useCallback((id: number) => {
    const newUsers = users.filter(user => user.id !== id);
    setUsers(newUsers);
    saveToStorage(newUsers);
}, [users, saveToStorage]);

return {
    users,
    isLoading,
    addUser,
    deleteUser
};
};