"use client";

import Link from "next/link";
import { useState } from "react";
import { useSQLite } from "./hooks/useSQLite";
import { useRouter } from "next/navigation";
import './globals.css';
import './simplified.css'; // –ù–æ–≤—ã–π —Ñ–∞–π–ª —Å —É–ø—Ä–æ—â–µ–Ω–Ω—ã–º–∏ —Å—Ç–∏–ª—è–º–∏

export default function Home() {
  const { users, isLoading, addUser, deleteUser } = useSQLite();
  const router = useRouter();
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [isRegistered, setIsRegistered] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (name.trim() && email.trim()) {
      addUser(name, email);
      setIsRegistered(true);
      setName('');
      setEmail('');
      
      setTimeout(() => {
        router.push('/dashboard');
      }, 2000);
    }
  };

  if (isRegistered) {
    return (
      <div className="page">
        <header className="header"></header>
        <main className="main">
          <div className="success-container">
            <div className="success-card">
              <h1 className="success-title">üéâ –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è —É—Å–ø–µ—à–Ω–∞!</h1>
              <p className="success-text">
                –î–æ–±—Ä–æ –ø–æ–∂–∞–ª–æ–≤–∞—Ç—å, <strong>{name}</strong>!
              </p>
              <p className="success-subtext">
                –ü–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —á–µ—Ä–µ–∑ 2 —Å–µ–∫—É–Ω–¥—ã...
              </p>
              
              <div className="redirect-info">
                <div className="spinner">‚è≥</div>
                <p>–ü–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ...</p>
              </div>
              
              <div className="links-container">
                <Link href="/dashboard" className="primary-link">
                  üìä –ü–µ—Ä–µ–π—Ç–∏ –≤ –ª–∏—á–Ω—ã–π –∫–∞–±–∏–Ω–µ—Ç —Å–µ–π—á–∞—Å
                </Link>
                <Link href="/" className="back-link">
                  –í—ã–π—Ç–∏
                </Link>
              </div>
            </div>
          </div>
        </main>
        <footer className="footer">
          –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è –∑–∞–≤–µ—Ä—à–µ–Ω–∞ —É—Å–ø–µ—à–Ω–æ
        </footer>
      </div>
    );
  }

  return (
    <div className="page">
      <header className="header"></header>
      <main className="main">
        
        {/* –§–æ—Ä–º–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ */}
        <div className="form-container">
          <h2 className="form-title">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è –Ω–æ–≤–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è</h2>
          <form onSubmit={handleSubmit} className="form">
            <input
              type="text"
              placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à–µ –∏–º—è"
              value={name}
              onChange={(e) => setName(e.target.value)}
              className="form-input"
              required
            />
            <input
              type="email"
              placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="form-input"
              required
            />
            <button type="submit" className="submit-btn">
              üìù –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è
            </button>
          </form>

          <div className="dashboard-link">
            <p>–£–∂–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã?</p>
            <Link href="/dashboard" className="dashboard-btn">
              üéâ –ü–µ—Ä–µ–π—Ç–∏ –≤ –ª–∏—á–Ω—ã–π –∫–∞–±–∏–Ω–µ—Ç
            </Link>
          </div>
        </div>

        {/* –°–ø–∏—Å–æ–∫ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π */}
        <div className="users-container">
          <h2 className="users-title">
            –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏ –≤ –±–∞–∑–µ –¥–∞–Ω–Ω—ã—Ö ({users.length})
          </h2>
          
          {isLoading ? (
            <p className="loading">–ó–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö...</p>
          ) : users.length === 0 ? (
            <p className="no-users">–ù–µ—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π –≤ –±–∞–∑–µ –¥–∞–Ω–Ω—ã—Ö</p>
          ) : (
            <div className="users-list">
              {users.map(user => (
                <div key={user.id} className="user-card">
                  <div className="user-info">
                    <strong>{user.name}</strong>
                    <span>{user.email}</span>
                  </div>
                  <button 
                    onClick={() => deleteUser(user.id)}
                    className="delete-btn"
                  >
                    –£–¥–∞–ª–∏—Ç—å
                  </button>
                </div>
              ))}
            </div>
          )}
        </div>

      </main>
      <footer className="footer">
        –°–∏—Å—Ç–µ–º–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π
      </footer>
    </div>
  );
}